.partners-block{
	width: 175px;
	height: 175px;
	overflow: hidden;
	margin: 0 20px;
	border-radius: 50%;
	display: inline-block;
	vertical-align: top;
	position: relative;
	@media (max-width: 1199px){
		margin: 0 5px;
	}
	@nest &--orange{
		background: #f48221;
	}
	@nest &--red{
		background: #ec2127;
	}
	@nest &--black{
		background: #000;
	}
	@nest &--blue{
		background: #0070ba;
	}
	@nest &--green{
		background: #00ab62;
	}
	@nest &__image{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		@nest &--indent{
			margin-top: -7px;
		}
	}
}