.header{
	width: 100%;
	padding: 20px 0 15px;
	background: #e82a29;
	@nest &-toggle{
		position: fixed;
		z-index: 100;
		left: 0;
		right: 0;
		top: 0;
	}
	@nest &__logo{
		margin-right: 55px;
		position: relative;
		padding: 0 0 0 70px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 1199px){
			margin-right: 40px;
		}
		@nest &:hover{
			text-decoration: none;
		}
	/* 	@nest &:hover &{
			&-image{
				animation: logo-rotate ease 1.5s;
			}
		} */
		@nest &:focus{
			text-decoration: none;
		}
		@nest &-image{
			position: absolute;
			left: 0;
			top: -11px;
		}
		@nest &-desc{
			font-size: 25px;
			color: #fff;
			font-weight: 700;
			line-height: 22px;
			@nest &--sm{
				font-size: 12px;
				font-weight: 400;
				display: block;
				line-height: 18px;
			}
		}
	}
	@nest &__right{
		float: right;
		text-align: right;
		line-height: normal;
		@nest &-text{
			font-size: 15px;
			color: #f6a2a1;
			display: block;
			@nest &--color{
				color: #fff;
			}
		}
	}
}

