.rules{
	padding: 65px 0 75px;
	@nest &__inner{
		clear: fix;
	}
	@nest &__item{
		width: 50%;
		float: left;
	}
	@nest &__title{
		font-size: 25px;
		color: #116faf;
		font-weight: 700;
		margin: 0 0 55px;
		display: block;
		letter-spacing: 1.5px;
	}
	@nest &__link{
		font-size: 15px;
		color: #116faf;
		text-decoration: underline;
		@nest &:hover{
			text-decoration: none;
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
}