.about-content{
	padding-bottom: 70px;
	border-bottom: 1px solid #e0e0e0;
	@nest &__inner{
		clear: fix;
	}
	@nest &__left{
		width: 100%;
		float: left;
		max-width: 800px;
		padding-right: 65px;
		@media (max-width: 1199px){
			max-width: 650px;
			padding-right: 40px;
		}
	}
	@nest &__right{
		width: 100%;
		float: right;
		max-width: 340px;
		text-align: right;
	}
	@nest &__title{
		font-size: 35px;
		color: #116faf;
		font-weight: 700;
		margin: 0 0 50px;
		letter-spacing: 2.1px;
	}
	@nest &__links{
		margin: 0 0 80px;
		@nest & .billboard-block__link{
			margin: 0 20px 0 0;
			display: inline-block;
		}
	}
}