.top-line{
	clear: fix;
	background: #f8f8f8;
	padding: 40px 0 110px;
	@nest &--indent-bot{
		margin-bottom: -70px;
	}
	@nest &--padding-bot{
		margin-bottom: -140px;
		padding-bottom: 150px;
	}
	@nest &--padding-md{
		margin-bottom: -110px;
		padding-bottom: 160px;
	}
	@nest &--padding-bot-lg{
		margin-bottom: -195px;
		padding-bottom: 200px;
	}
	@nest &__nav{
		clear: fix;
		margin: 0 0 30px;
	}
	@nest &__bot{
		font-size: 0;
	}
	@nest &__title{
		width: 33%;
		margin: 0;
		font-size: 35px;
		color: #116faf;
		font-weight: 700;
		letter-spacing: 2.1px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 1199px){
			width: 30%;
			font-size: 32px;
		}
		@nest &--size{
			@media (max-width: 1199px){
				width: 33%;
				font-size: 35px;
			}
		}
	}
}