.about-item{
	width: 33%;
	display: inline-block;
	vertical-align: top;
	@nest &__top{
		margin: auto;
		max-width: 262px;
		position: relative;
		overflow: hidden;
	}
	@nest &__logo{
		margin: auto;
		border-radius: 50%;
		@nest &--sm{
			width: 160px;
			height: 160px;
			z-index: 10;
			left: 102px;
			z-index: 1;
			margin-left: 0;
			margin-top: 44px;
			background: #003f6a;
			position: relative;
		}
		@nest &--left{
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			z-index: 10;
			margin-top: 0;
			background: #116faf;
			@nest &:after{
				position: absolute;
				content: '';
				bottom: -36px;
				left: 82px;
				background: resolve('about-arrow.png');
				width: width('about-arrow.png');
				height: height('about-arrow.png');
			}
		}
		@nest &-content{
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			text-align: center;
			transform: translate(-50%, -50%);
		}
		@nest &--lg{
			width: 203px;
			height: 203px;
			background: #116faf;
		}
		@nest &--border{
			background: transparent;
			border: 2px solid #1370b0;
		}
	}
	@nest &__title{
		color: #fff;
		font-weight: 700;
		@nest &--lg{
			font-size: 66px;
			line-height: 65px;
		}
		@nest &--md{
			font-size: 60px;
			line-height: 66px;
		}
		@nest &--sm{
			font-size: 45px;
			line-height: 49px;
		}
		@nest &--blue{
			color: #003f6a;
		}
		@nest &--indent{
			margin-left: 20px;
			white-space: nowrap;
			@nest & span{
				margin-left: 15px;
				position: relative;
				display: inline-block;
				line-height: 53px;
				@nest &:after{
					position: absolute;
					content: '';
					left: -7px;
					bottom: 6px;
					width: 34px;
					height: 8px;
					background: #fff;
					border-radius: 4px;
				}
			}
		}
	}
	@nest &__text{
		display: block;
		color: #fff;
		font-weight: 700;
		line-height: normal;
		@nest &--lg{
			font-size: 32px;
			line-height: 35px;
		}
		@nest &--md{
			font-size: 24px;
			letter-spacing: 1.5px;
		}
		@nest &--sm{
			font-size: 18px;
		}
		@nest &--blue{
			color: #003f6a;
		}
		@nest &--indent{
			margin-bottom: 10px;
		}
		@nest &--indent-bot{
			margin-bottom: 5px;
		}
	}
	@nest &__desc{
		display: block;
		font-size: 14px;
		color: #000;
		text-align: center;
		max-width: 235px;
		margin: 25px auto 0;
		letter-spacing: .96px;
	}
}