.nav-list{
	padding: 0;
	margin: 0;
	list-style: none;
	@nest &__item{
		margin: 0 20px;
		display: inline-block;
		@media (max-width: 1199px){
			margin: 0 10px;
		}
		@nest &--footer{
			margin: 0 70px 0 0;
			@nest &:last-child{
				margin-right: 0;
			}
			@media (max-width: 1199px){
				margin: 0 45px 0 0;
			}
		}
	}
	@nest &__link{
		font-size: 15px;
		color: #fff;
		padding: 0 10px;
		position: relative;
		display: inline-block;
		transition: color 300ms;
		@nest &:hover{
			color: #5c0505;
			text-decoration: none;
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: -25px;
			width: 100%;
			height: 1px;
			background: #fff;
			opacity: 0;
			transition: opacity 300ms;
		}
		@nest &--footer{
			padding: 0;
			color: #979797;
			@nest &:hover{
				color: #000;
			}
			@nest &:after{
				background: #000;
				bottom: -5px;
			}
		}
	}
}