.footer{
	padding: 40px 0;
	background: #f2f2f2;
	@nest &__inner{
		font-size: 0;
		margin-bottom: 30px;
		@nest &:last-child{
			margin-bottom: 0;
		}
	}
	@nest &__item{
		width: 50%;
		display: inline-block;
		vertical-align: top;
	}
	@nest &__copy{
		display: block;
		font-size: 15px;
		color: #979797;
		line-height: normal;
		letter-spacing: .495;
	}
	@nest &__cont{
		font-size: 15px;
		color: #979696;
		display: inline-block;
		@nest &--mail{
			color: #116faf;
			margin-left: 5px;
			text-decoration: underline;
		}
		@nest &:hover{
			text-decoration: none;
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
	@nest &__bot{
		clear: fix;
		@nest &-app{
			float: left;
		}
		@nest &-social{
			float: right;
		}
		@nest &-title{
			font-size: 15px;
			color: #979797;
			display: inline-block;
			vertical-align: bottom;
		}
		@nest &-link{
			margin-left: 20px;
			display: inline-block;
			vertical-align: bottom;
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
			@media (max-width: 1199px){
				margin-left: 10px;
			}
		}
		@nest &-soc-link{
			margin-left: 15px;
			display: inline-block;
			vertical-align: middle;
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
			@media (max-width: 1199px){
				margin-left: 10px;
			}
		}
	}
}