.cabinet-info{
	clear: fix;
	@nest &__item{
		width: 33.33%;
		float: left;
	}
	@nest &__table{
		display: table;
		@nest &--indent-left{
			margin-left: 43px;
			@media (max-width: 1199px){
				margin-left: 18.5px;
			}
		}
		@nest &--float-right{
			float: right;
			@nest & .cabinet-info__cell{
				@nest &--right{
					text-align: right;
				}
			}
		}
	}
	@nest &__row{
		display: table-row;
	}
	@nest &__cell{
		font-size: 15px;
		color: #898989;
		padding: 0 10px 15px 0;
		display: table-cell;
		@nest &--right{
			color: #000;
			padding-right: 0;
		}
	}
}