.modal-form{
	max-width: 330px;
	margin: auto;
	@nest &__inp{
		width: 100%;
		height: 48px;
		color: #000;
		outline: none;
		font-size: 20px;
		text-align: center;
		margin-bottom: 45px;
		border-radius: 10px;
		letter-spacing: 1.2px;
		border: 1px solid #0489e3;
		@nest &--border{
			border-color: #acacac;
		}
		@nest &::placeholder{
			color: #a2a2a2;
			font-size: 20px;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		outline: none;
		height: 45px;
		width: 225px;
		display: block;
		margin: 5px auto 0;
		border-radius: 8px;
		background: #0489e3;
		letter-spacing: .72px;
	}
}