.modal{
	display: none;
	width: 100%;
	padding: 40px 0 45px;
	width: 645px;
	overflow: hidden;
	text-align: center;
	border-radius: 20px;
	background: #fff;
	box-shadow: 0px 29px 40px 0px rgba(84, 3, 3, 0.2);
	@nest &:after{
		position: absolute;
		content: '';
		top: 83px;
		right: -173px;
		background: resolve('modal-image.png');
		width: width('modal-image.png');
		height: height('modal-image.png');
	}
	@nest &--bg{
		@nest &:after{
			display: none;
		}
	}
