		@nest &__title{
			font-size: 25px;
			color: #116faf;
			font-weight: 700;
			margin: 0 0 50px;
			display: block;
			letter-spacing: 1.5px;
		}
	}
	.modal-active{
		height: 100% !important;
		overflow: hidden !important;
	}

	.fancybox-skin{
		padding: 0 !important;
	}
	.fancybox-inner{
		overflow: hidden !important;
	}
	.fancybox-opened .fancybox-skin{
		box-shadow: none !important;
	}
	.fancybox-skin{
		background: transparent !important;
	}
	.fancybox-overlay-fixed{
		width: 1022px;
	}
	.fancybox-close{
		top: 28px;
		right: 28px;
		background: resolve('close.png');
		width: width('close.png');
		height: height('close.png');
	}