.about{
	padding: 65px 0 55px;
	background: #f2f2f2;
	@nest &__content{
		clear: fix;
		padding: 0 0 30px 115px;
		border-bottom: 1px solid #969696;
	}
	@nest &__left{
		float: left;
		width: 100%;
		max-width: 495px;
	}
	@nest &__right{
		float: right;
	}
	@nest & h3{
		font-size: 61px;
		color: #116faf;
		margin-bottom: 30px;
		line-height: normal;
	}
	@nest & p{
		font-size: 17px;
		color: #525151;
		display: block;
		line-height: 26px;
		margin: 5px 0 30px;
		letter-spacing: .66px;
	}
	@nest &__items{
		font-size: 0;
		margin: 55px 0 0;
	}
}