.how-block{
	margin: 0 0 0 30px;
	max-width: 190px;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	position: relative;
	@media (max-width: 1199px){
		margin-left: 5px;
	}
	@nest &:last-child &{
		@nest &__logo{
			@nest &:after{
				display: none;
			}
		}
	}
	@nest &:hover{
		text-decoration: none;
	}
	@nest &:hover &{
		&__logo{
			background-color: #116faf;
		}
	}
	@nest &:focus{
		text-decoration: none;
	}
	@nest &__logo{
		display: block;
		width: 136px;
		height: 136px;
		margin: auto;
		margin-bottom: 20px;
		border-radius: 50%;
		border: 5px solid #116faf;
		transition: background-color 300ms;
		@nest &:after{
			position: absolute;
			content: '';
			top: 61px;
			right: -27.5px;
			background: resolve('how-arrow.png');
			width: width('how-arrow.png');
			height: height('how-arrow.png');
			@media (max-width: 1199px){
				right: -16px;
			}
		}
	}
	@nest &__desc{
		display: block;
		font-size: 14px;
		color: #000;
		line-height: normal;
	}
}