.page-nav{
	margin: 15px 0 50px;
	text-align: center;
	@nest &--indent{
		margin: 35px 0 10px;
	}
	@nest &__link{
		font-size: 20px;
		color: #116faf;
		font-weight: 700;
		margin: 0 27px;
		line-height: normal;
		letter-spacing: 1.2px;
		display: inline-block;
		vertical-align: middle;
		@nest &--active{
			font-size: 35px;
			color: #e82a29;
			letter-spacing: 2.1px;
		}
		@nest &--points{
			margin: 0;
		}
		@nest &:hover{
			text-decoration: none;
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
}