.user-block{
	@nest &__image{
		border-radius: 20px;
	}
	@nest &__link{
		font-size: 15px;
		color: #fff;
		display: block;
		line-height: 38px;
		background: #0489e3;
		border-radius: 8px;
		margin: 25px 0 0;
		text-align: center;
		border: 1px solid transparent;
		transition: border 300ms, color 300ms, background 300ms;
		@nest &:hover{
			color: #0489e3;
			border-color: #0489e3;
			text-decoration: none;
			background: transparent;
		}
		@nest &:focus{
			text-decoration: none;
		}
		@nest &--bg{
			color: #0489e3;
			background: transparent;
			border: 1px solid #0489e3;
			@nest &:hover{
				color: #fff;
				background: #0489e3;
				border-color: transparent;
			}
		}
	}
	@nest &__desc{
		display: block;
		font-size: 15px;
		color: #898989;
		max-width: 190px;
		margin: 10px auto 0;
		text-align: center;
		line-height: normal;
		@nest &--color{
			color: #0489e3;
		}
	}
}