.search{
	float: right;
	@nest &__inp{
		font-size: 14px;
		color: #888;
		border: 0;
		outline: none;
		max-width: 125px;
		margin-right: 15px;
		padding-left: 10px;
		letter-spacing: .9px;
		background: transparent;
		display: inline-block;
		vertical-align: middle;
		transition: background 300ms;
		@nest &::placeholder{
			font-size: 15px;
			color: #888;
			letter-spacing: .9px;
		}
		@nest &:focus{
			background: #fff;
		}
	}
	@nest &__btn{
		border: 0;
		padding: 0;
		outline: none;
		font-size: 0;
		background: transparent;
		display: inline-block;
		vertical-align: middle;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
	}
}