.sorting-nav{
	padding: 0;
	margin: 0;
	width: 25%;
	float: left;
	list-style: none;
	text-align: left;
	padding-right: 20px;
	@nest &:last-child{
		padding-right: 0;
	}
	@nest &__item{
		display: block;
		margin: 30px 0 0;
	}
	@nest &__link{
		font-size: 15px;
		color: #000;
		position: relative;
		line-height: normal;
		padding: 0 0 0 40px;
		display: inline-block;
		transition: color 300ms;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			margin-top: -9.5px;
			transition: background 300ms;
			background: resolve('backet.png');
			width: width('backet.png');
			height: height('backet.png');
		}
		@nest &:hover{
			color: #e82a29;
			text-decoration: none;
			@nest &:after{
				background: resolve('backet-h.png');
				width: width('backet-h.png');
				height: height('backet-h.png');
			}
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
}