.cabinet-links{
	width: 67%;
	text-align: right;
	display: inline-block;
	vertical-align: bottom;
	@nest &__item{
		font-size: 15px;
		color: #000;
		margin: 0 25px;
		line-height: 27px;
		padding: 0 10px;
		position: relative;
		letter-spacing: .36px;
		display: inline-block;
		vertical-align: middle;
		transition: color 300ms;
		@nest &:hover{
			color: #0489e3;
			text-decoration: none;
		}
		@nest &:focus{
			text-decoration: none;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: -5px;
			height: 1px;
			opacity: 0;
			background: #0489e3;
			transition: opacity 300ms;
		}
		@nest &:first-child{
			
		}
		@nest &:last-child{
			margin-right: 0;
		}
		@nest &--active{
			color: #0489e3;
			font-weight: 700;
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &--msg{
			padding-right: 0;
			margin-right: 5px;
		}
	}
	@nest &__msg{
		height: 27px;
		color: #fff;
		padding: 0 6px;
		display: block;
		font-size: 15px;
		min-width: 27px;
		margin: 0 0 0 3px;
		line-height: 27px;
		text-align: center;
		background: #e82a29;
		border-radius: 27px;
		display: inline-block;
	}
}