.sorting-select{
	width: 100%;
	cursor: pointer;
	outline: none;
	z-index: 100;
	max-width: 210px;
	margin: 0 0 0 10px;
	border-radius: 10px;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	@media (max-width: 1199px){
		max-width: 175px;
		background-position: 150px center;
	}
	@nest &__title{
		font-size: 16px;
		color: #000;
		height: 38px;
		display: block;
		line-height: 38px;
		text-align: left;
		letter-spacing: .9px;
		padding: 0 0 0 15px;
		position: relative;
		z-index: 100;
		background: #fff;
		border: 1px solid #acacac;
		background: resolve('select-arrow.png') no-repeat 185px center;
		@nest &--active{
			border: 0;
			background: #fff;
			position: relative;
			border-radius: 10px 10px 0 0;
			box-shadow: 0px 3px 40px 0px rgba(2, 28, 46, 0.2);
			background: #fff resolve('select-arrow-top.png') no-repeat 185px center;
		}
		@media (max-width: 1199px){
			background-position: 150px center;
		}
	}
	@nest &__hidden{
		position: absolute;
		content: '';
		top: 100%;
		right: 0;
		width: 1140px;
		display: none;
		background: #fff;
		padding: 5px 35px 40px;
		border-radius: 10px 0 10px 10px;
		box-shadow: 0px 3px 40px 0px rgba(2, 28, 46, 0.2);
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 0;
			right: 0;
			height: 35px;
			z-index: 101;
			background: #fff;
		}
		@media (max-width: 1199px){
			width: 990px;
		}
	}
}