.partners{
	padding: 80px 0 85px;
	background: #fff;
	text-align: center;
	@nest &__inner{
		margin: 110px 0;
	}
	@nest &__link{
		font-size: 23px;
		color: #fff;
		width: 252px;
		line-height: 67px;
		border-radius: 8px;
		background: #116faf;
		letter-spacing: 1.38px;
		display: inline-block;
		transition: background 300ms;
		@nest &:hover{
			text-decoration: none;
			background: #0b578b;
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
}