.how{
	position: relative;
	background: #fff;
	@nest &__inner-bg{
		position: absolute;
		content: '';
		top: 0;
		left: 50%;
		bottom: 0;
		width: 100%;
		max-width: 1600px;
		transform: translateX(-50%);
		background: #fff resolve('how.jpg') -146px bottom no-repeat;
		@media (max-width: 1199px){
			background-position: -230px bottom;
		}
		@media (min-width: 1600px){
			background-position: left bottom;
		}
	}
	@nest &__inner{
		overflow: hidden;
		min-height: 770px;
		position: relative;
		max-width: 1600px;
		margin: auto;
		padding: 115px 0 0;
	}
	@nest & .container{
		position: relative;
		z-index: 10;
	}
	@nest &__links{
		font-size: 0;
		margin: 150px 0 0;
		text-align: right;
	}
}
.parallax-viewport{
	position: absolute;
	content: '';
	top: 0;
	z-index: 1;
	right: 50px;
	width: 840px;
	height: 100%;
	overflow: visible;
	@nest & img{
		position: relative;
	}
}
.parallax-layer{
	font-size: 0;
	width: 890px;
	height: 105%;
	@nest &--height{
		width: 790px;
		bottom: 0;
		height: calc(100% - 50px);
	}
	@nest &--opacity1{
		@nest & img{
			animation: opacity 5s ease 2s infinite;
		}
	}
	@nest &--opacity2{
		@nest & img{
			animation: opacity2 5s ease 2s infinite;
		}
	}
	@nest &--zoom{
		@nest & img{
			animation: zoom 4s ease 2s infinite;
		}
	}
	@nest &--rotate{
		@nest & img{
			animation: rotate 4s ease 2s infinite;
		}
	}
	@nest & img{
		position: absolute;
	}
}

@keyframes opacity{
	47%{
		opacity: 1;
	}
	50%{
		opacity: 0;
	}
	53%{
		opacity: 1;
	}
}

@keyframes opacity2{
	47%{
		opacity: 1;
	}
	50%{
		opacity: 0;
	}
	53%{
		opacity: 1;
	}
	56%{
		opacity: 1;
	}
	59%{
		opacity: 0;
	}
	62%{
		opacity: 1;
	}
}

@keyframes zoom{
	48%{
		transform: scale(1);
	}
	49%{
		transform: scale(0);
	}
	50%{
		transform: scale(1);
	}
	51%{
		transform: scale(.8);
	}
	53%{
		transform: scale(1);
	}
}

@keyframes rotate{
	70%{
		transform: rotate(0);
	}
	85%{
		transform: rotate(360deg);
	}
	100%{
		transform: rotate(360deg);
	}
}