.sorting{
	width: 33%;
	margin: 2px 0 0;
	text-align: center;
	display: inline-block;
	vertical-align: middle;
	@media (max-width: 1199px){
		width: 40%;
	}
	@nest &--right{
		width: 34%;
		text-align: right;
		@media (max-width: 1199px){
			width: 30%;
		}
	}
	@nest &__title{
		font-size: 16px;
		color: #010101;
		letter-spacing: .96px;
		display: inline-block;
		vertical-align: middle;
	}
	@nest &__btn{
		font-size: 16px;
		border: 0;
		outline: none;
		margin: 0 0 0 20px;
		padding: 0 30px 0 0;
		position: relative;
		letter-spacing: .96px;
		display: inline-block;
		vertical-align: middle;
		background: transparent;
		color: color(#000 alpha(60%));
		@nest &:before{
			position: absolute;
			content: '';
			top: 50%;
			right: 11px;
			transform: translateY(-50%);
			background: resolve('sort-top.png');
			width: width('sort-top.png');
			height: height('sort-top.png');
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: 50%;
			right: 0;
			transform: translateY(-50%);
			background: resolve('sort-bot.png');
			width: width('sort-bot.png');
			height: height('sort-bot.png');
		}
		@nest &--active-top{
			color: #e82a29;
			@nest &:before{
				background: resolve('sort-top-active.png');
				width: width('sort-top-active.png');
				height: height('sort-top-active.png');
			}
		}
		@nest &--active-bot{
			color: #e82a29;
			@nest &:after{
				background: resolve('sort-bot-active.png');
				width: width('sort-bot-active.png');
				height: height('sort-bot-active.png');
			}
		}
	}
	@nest &__select{
		font-size: 16px;
		color: #000;
		width: 100%;
		height: 38px;
		cursor: pointer;
		outline: none;
		max-width: 210px;
		margin: 0 0 0 10px;
		border-radius: 10px;
		padding: 0 0 0 15px;
		letter-spacing: .9px;
		display: inline-block;
		vertical-align: middle;
		-webkit-appearance: none;
		-moz-appearance: none;
		-ms-appearance: none;
		appearance: none!important;
		border: 1px solid #acacac;
		background: resolve('select-arrow.png') no-repeat 185px center;
		@media (max-width: 1199px){
			max-width: 175px;
			background-position: 150px center;
		}
	}
}