.shares-content{
	padding: 0 0 40px;
	border-bottom: 1px solid #e0e0e0;
	@nest &__inner{
		clear: fix;
	}
	@nest &__left{
		width: 100%;
		float: left;
		max-width: 230px;
	}
	@nest &__right{
		width: 100%;
		float: left;
		max-width: 910px;
		padding-left: 50px;
		@media (max-width: 1199px){
			max-width: 760px;
		}
	}
	@nest &__title{
		font-size: 20px;
		color: #000;
		font-weight: 700;
		line-height: normal;
		letter-spacing: 1.2px;
		margin: 0 0 25px;
	}
	@nest &__date{
		display: block;
		font-size: 16px;
		color: #000;
		letter-spacing: .96px;
		margin-bottom: 25px;
		@nest &-title{
			color: #929292;
		}
	}
	@nest &__days{
		display: block;
		font-size: 15px;
		color: #e82a29;
		letter-spacing: .9px;
		margin-bottom: 25px;
	}
	@nest &__title-sm{
		font-size: 25px;
		color: #116faf;
		font-weight: 700;
		margin: 70px 0 0;
		letter-spacing: 1.5px;
		line-height: normal;
		@nest &--indent{
			margin-top: 50px;
		}
		@nest &--center{
			margin: 45px 0;
			text-align: center;
		}
	}
	@nest &__list{
		padding: 0;
		margin: 15px 0 0;
		list-style: none;
		@nest &-item{
			font-size: 15px;
			color: #000;
			letter-spacing: .9px;
			line-height: normal;
		}
	}
	@nest &__link{
		font-size: 18px;
		color: #fff;
		padding: 0 30px;
		line-height: 53px;
		display: inline-block;
		background: #0489e3;
		border-radius: 8px;
		margin: 30px 0 10px;
		transition: background 300ms;
		@nest &:hover{
			text-decoration: none;
			background: #0c649f;
		}
		@nest &:focus{
			text-decoration: none;
		}
	}
}
p{
	font-size: 15px;
	color: #000;
	margin: 20px 0;
	letter-spacing: .9px;
	line-height: normal;
}