.shares-block{
	background: #fff;
	position: relative;
	border-radius: 20px;
	margin: 0 0 50px;
	display: inline-block;
	padding: 30px 25px 20px;
	border: 1px solid #ebebeb;
	transition: border-color 300ms, box-shadow 300ms;
	@nest &:hover{
		text-decoration: none;
		border-color: #0489e3;
		box-shadow: 0px 3px 40px 0px rgba(2, 28, 46, 0.2);
	}
	@nest &:focus{
		text-decoration: none;
	}
	@nest &--single{
		margin: 0;
		width: 100%;
		min-height: 230px;
		@nest & .shares-block__image{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
	@nest &__tooltip{
		position: absolute;
		content: '';
		top: -1px;
		right: -1px;
		height: 108px;
		width: 108px;
		@nest &:after{
			position: absolute;
			content: '';
			top: 0px;
			right: 0px;
			z-index: 1;
			border-radius: 0 20px 0 0;
			border: 54px solid transparent;
			border-top: 54px solid #0489e3;
			border-right: 54px solid #0489e3;
		}
		@nest &-text{
			position: absolute;
			content: '';
			top: 20.5px;
			left: 20.5px;
			width: 100%;
			line-height: 25px;
			z-index: 10;
			color: #fff;
			font-size: 25px;
			display: block;
			text-align: center;
			transform: rotate(45deg);
		}
	}
	@nest &__image{
		width: 132px;
		height: 132px;
		margin: auto;
		overflow: hidden;
		border-radius: 50%;
		position: relative;
		@nest &--border{
			border: 1px solid #b6b6b6;
		}
		@nest &-in{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
	@nest &__title{
		font-size: 18px;
		color: #000;
		display: block;
		font-weight: 700;
		margin: 30px 0 0;
		line-height: normal;
		letter-spacing: 1.08px;
	}
	@nest &__desc{
		font-size: 15px;
		color: #777;
		display: block;
		margin: 20px 0 0;
		line-height: normal;
		letter-spacing: .9px;
	}
}