.history{
	@nest &__title{
		font-size: 25px;
		color: #116faf;
		font-weight: 700;
		margin: 40px 0 5px;
		letter-spacing: 1.5px;
		line-height: normal;
	}
	@nest & table{
		width: 100%;
		@nest & thead{
			@nest & tr{
				@nest &:last-child{
					border-bottom: 1px solid #ececec;
				}
			}
			@nest & td{
				color: #a2a2a2;
				@nest &:last-child{
					color: #a2a2a2;
				}
			}
		}
		@nest & tr{
			border-bottom: 1px solid #ececec;
			@nest &:last-child{
				border-bottom: 0;
			}
		}
		@nest & td{
			font-size: 15px;
			color: #000;
			padding: 29px 80px 29px 0;
			letter-spacing: .9px;
			@nest &:last-child{
				color: #4acd02;
				font-weight: 700;
				padding-right: 0;
				padding-left: 60px;
				text-align: center;
				@media (max-width: 1199px){
					padding-left: 0;
				}
			}
			@media (max-width: 1199px){
				padding-right: 0;
			}
			@nest &.red{
				color: #ff0101;
			}
			@nest & span{
				font-size: 20px;
				color: #fff;
				height: 31px;
				width: 31px;
				font-weight: 700;
				padding-left: 2px;
				background: #febc38;
				border-radius: 50%;
				line-height: 31px;
				margin-right: 7px;
				text-align: center;
				display: inline-block;
			}
		}
	}
}