.billboard-block{
	position: fixed;
	width: 100%;
	width: 520px;
	margin-top: 150px;
	@nest &__title{
		display: block;
		color: #fff;
		margin: 0;
		font-weight: 700;
		@nest &--sm{
			font-size: 28px;
			line-height: normal;
		}
		@nest &--lg{
			font-size: 122px;
			line-height: 116px;
			margin-bottom: 70px;
		}
	}
	@nest &__bonuses{
		clear: fix;
		margin-bottom: 85px;
		@nest &-image{
			float: left;
			margin-right: 30px;
		}
		@nest &-text{
			display: block;
			font-size: 18px;
			color: #fff;
			float: left;
			max-width: 350px;
			line-height: normal;
			letter-spacing: 1.4px;
		}
	}
	@nest &__link{
		width: 240px;
		font-size: 15px;
		color: #fff;
		line-height: 24px;
		text-decoration: none;
		display: inline-block;
		position: relative;
		border-radius: 7px;
		margin-right: 20px;
		padding: 7px 0 8px 80px;
		background: #f64434;
		background: linear-gradient(to top, #e72528, #f64434);
		@nest &:hover{
			text-decoration: none;
			background: #e72528;
			background: linear-gradient(to top, #f64434, #e72528);
		}
		@nest &:focus{
			text-decoration: none;
		}
		@nest &:last-child{
			margin-right: 0;
		}
		@nest & span{
			font-size: 23px;
			display: block;
			line-height: normal;
		}
		@nest & img{
			position: absolute;
			left: 25px;
			top: 50%;
			margin-top: -18.5px;
		}
		@nest &--black{
			display: block;
			margin: 0 0 35px;
			background: #000;
			transition: background 300ms;
			@nest &:hover{
				background: #191717;
			}
		}
	}
}